___CSS_LOADER_EXPORT___.push([module.id, "\n#balekMainScreenshot {\n  max-width: 80%;\n}\n.digivigilMainLogo {\n  width: 100px;\n  height: 100px;\n  padding: 0;\n  margin: 0;\n}\n", "",{"version":3,"sources":["webpack://./src/components/digivigil/Info.vue"],"names":[],"mappings":";AA2DA;EACE,cAAc;AAChB;AAEA;EACE,YAAY;EACZ,aAAa;EACb,UAAU;EACV,SAAS;AACX","sourcesContent":["<template>\n  <div class=\"MainInfoTitleDiv\">\n    <img\n      class=\"MainInfoTitleLogoImg\"\n      style=\"filter: brightness(50%)\"\n      :src=\"DigivigilLogo\"\n      alt=\"Balek Logo\"\n    />\n    Digivigil\n    <div class=\"MainInfoSummary\">Independent Development House<br /></div>\n  </div>\n\n  <br />\n  <div class=\"MainInfoAndLogo\">\n    <div class=\"mainLogoDiv\">\n      <img class=\"digivigilMainLogo\" :src=\"DigivigilLogo\" alt=\"Digivigil\" />\n    </div>\n    <br />\n  </div>\n  <br />\n  <div class=\"MainInfoDiv\">\n    Welcome to Digivigil, a software development brand focused on creating\n    high-quality solutions for the digital age. Currently, our team consists of\n    a single developer who is dedicated to delivering robust tools for utilizing\n    technology to users of various experience levels through our two exciting\n    projects: Balek, a full-stack Javascript (Transitioning to TypeScript)\n    framework, and Diaplode, a remote workstation web application built on\n    Balek.\n    <br />\n    <br />\n    We understand the importance of collaboration and are always open to\n    contributions from interested parties. Our mission is to create software\n    that is highly accessible and can be used to solve real-world problems.\n    <br />\n    <br />\n    To achieve this goal, we are committed to taking several steps, including\n    refactoring our codebase from JavaScript to TypeScript, updating our\n    documentation, and creating comprehensive unit tests. We regularly update\n    our progress on Github, where interested parties can follow our journey and\n    contribute to our projects.\n    <br />\n    <br />\n    Thank you for your interest in Digivigil, and we look forward to sharing our\n    progress with you.\n  </div>\n</template>\n<script>\nimport DigivigilLogo from \"./resources/digivigil.svg\";\n\nexport default {\n  name: \"DigivigilInfo\",\n  data: function () {\n    return {\n      DigivigilLogo,\n    };\n  },\n};\n</script>\n<style>\n#balekMainScreenshot {\n  max-width: 80%;\n}\n\n.digivigilMainLogo {\n  width: 100px;\n  height: 100px;\n  padding: 0;\n  margin: 0;\n}\n</style>\n"],"sourceRoot":""}]);
